'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';

import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Alert,
  AlertDescription,
  AlertTitle,
} from '@/components/ui/alert';
import { Skeleton } from '@/components/ui/skeleton';

import type { InstagramMedia } from '@/lib/instagram';

export default function InstagramPostsPage() {
  const [posts, setPosts] = useState<InstagramMedia[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const loadPosts = async () => {
      setIsLoading(true);
      setError(null);
      try {
        const response = await fetch('/api/instagram/fetch-posts');
        const payload = await response.json();
        if (!response.ok) {
          throw new Error(payload?.error ?? 'Failed to load posts.');
        }
        setPosts(payload.data ?? []);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : '投稿の取得に失敗しました。',
        );
      } finally {
        setIsLoading(false);
      }
    };

    void loadPosts();
  }, []);

  const mediaTypeLabel = (type: InstagramMedia['media_type']): string => {
    switch (type) {
      case 'VIDEO':
        return 'Video';
      case 'IMAGE':
        return 'Image';
      case 'CAROUSEL_ALBUM':
        return 'Carousel';
      default:
        return 'Post';
    }
  };

  const renderContent = () => {
    if (isLoading) {
      return (
        <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {Array.from({ length: 6 }).map((_, index) => (
            <Card key={`skeleton-${index}`} className="space-y-3 p-4">
              <Skeleton className="h-48 w-full rounded-lg" />
              <Skeleton className="h-4 w-2/3" />
              <Skeleton className="h-4 w-1/2" />
              <Skeleton className="h-9 w-full" />
            </Card>
          ))}
        </div>
      );
    }

    if (error) {
      return (
        <Alert variant="destructive">
          <AlertTitle>エラー</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      );
    }

    if (!posts.length) {
      return (
        <Alert>
          <AlertTitle>投稿が見つかりません</AlertTitle>
          <AlertDescription>
            Instagramに最新の投稿があるか確認し、再度取得してください。
          </AlertDescription>
        </Alert>
      );
    }

    return (
      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
        {posts.map((post) => (
          <Card key={post.id} className="flex flex-col">
            <CardHeader className="space-y-2">
              <div className="flex items-center justify-between">
                <Badge variant="secondary">{mediaTypeLabel(post.media_type)}</Badge>
                {post.timestamp ? (
                  <span className="text-xs text-muted-foreground">
                    {new Date(post.timestamp).toLocaleString('ja-JP', {
                      dateStyle: 'medium',
                      timeStyle: 'short',
                    })}
                  </span>
                ) : null}
              </div>
              <CardTitle className="line-clamp-2 text-sm font-medium">
                {post.caption ?? 'キャプション未設定'}
              </CardTitle>
            </CardHeader>
            <CardContent className="flex-1 space-y-3">
              {post.thumbnail_url || post.media_url ? (
                <div className="overflow-hidden rounded-md border">
                  <img
                    src={post.thumbnail_url ?? post.media_url ?? ''}
                    alt={post.caption ?? 'Instagram thumbnail'}
                    className="aspect-video w-full object-cover"
                    loading="lazy"
                  />
                </div>
              ) : null}
              <div className="flex items-center gap-2">
                <Button variant="secondary" asChild>
                  <Link
                    href={post.permalink ?? '#'}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Instagramで見る
                  </Link>
                </Button>
                <Button asChild>
                  <Link href={`/videos/new?ig_media_id=${post.id}`}>
                    タグ付けする
                  </Link>
                </Button>
              </div>
            </CardContent>
            <CardFooter>
              <p className="text-xs text-muted-foreground truncate">
                Media ID: {post.id}
              </p>
            </CardFooter>
          </Card>
        ))}
      </div>
    );
  };

  return (
    <div className="space-y-6">
      <header className="space-y-2">
        <h1 className="text-2xl font-bold">Instagram投稿の取り込み</h1>
        <p className="text-sm text-muted-foreground">
          最新のInstagram投稿を取得して、分析タグを付ける動画を選択してください。
        </p>
      </header>
      {renderContent()}
    </div>
  );
}
